<template>
    <div class="notes">
        <div class="note" :class="{ full: !grid, warning: note.notePriority == 'medium-priority', 'high-priority': note.notePriority == 'high-priority' }" v-for="(note, index) in notes" :key="index">
            <div class="note-header" :class="{ full: !grid }">
                <p>{{ note.title }}</p>
                <p style="cursor: pointer;" @click="removeNote(index)">×</p>
            </div>
            <div class="note-body">
                <p>{{ note.descr }}</p>
                <span>{{ note.date }}</span>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    props: {
        notes:{
            type: Array,
            required: true
        },
        grid: {
            type: Boolean,
            required: true
        },
    },
    methods: {
        removeNote (index) {
            this.$emit('remove', index)
        }
    }
}
</script>


<style lang="scss">
.notes {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 40px 0;
}

.note {
    border: 1px solid #DCDFE6;
    border-radius: 14px;
    width: 48%;
    padding: 18px 20px;
    margin-bottom: 20px;
    background-color: #fff;
    transition: transform .25s cubic-bezier(.02, .01, .47, 1), box-shadow .25s cubic-bezier(.02, .01, .47, 1);
    box-shadow: 0 30px 30px rgba(0, 0, 0, .02);
    &:hover {
        box-shadow: 0 30px 30px rgba(0, 0, 0, .04);
        transform: translate(0, -6px);
        transition-delay: 0s !important;
    }
    &.full {
        width: 100%;
        text-align: center;
    }
}

.note-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h1 {
        font-size: 32px;
    }
    svg {
        color: #999;
        cursor: pointer;
        &.active {
            color: #5747b3;
        }
        &:not(:last-child) {
            margin-right: 12px;
        }
    }
    p {
        font-size: 22px;
        color: #5747b3;
        &:not(:first-child) {
            &:hover {
                color: #999;
            }
        }
    }
    &.full {
        justify-content: center;
        p{
            &:not(:last-child) {
                margin-right: 16px;
            }
        }
    }
}

.note-body {
    p {
        margin: 20px 0;
    }
    span {
        font-size: 14px;
        color: #999;
    }
}
</style>
