<template>
    <div class="new-note">
      <label><b>Title</b></label>
      <div class="radio-priority">
        <div class="radio">
          <label for="norPr">normal</label>
          <input type="radio" id="norPr" name="typeOfPriority" value="normal" v-model="note.notePriority" checked>
        </div>
        <div class="radio">
          <label for="midPr">medium-priority</label>
          <input type="radio" id="midPr" name="typeOfPriority" value="medium-priority" v-model="note.notePriority">
        </div>
        <div class="radio">
          <label for="highPr">high-priority</label>
          <input type="radio" id="highPr" name="typeOfPriority" value="high-priority" v-model="note.notePriority">
        </div>
      </div>

      <input v-model="note.title" type="text"><br>
      <label><b>Description</b></label>
      <textarea v-model="note.descr"></textarea><br>
      <button class="btn btnPrimary" @click="addNote">new note</button>
    </div>
</template>

<script>
  export default {
      props: {
          note: {
              type: Object,
              required: true
          },
      },
      methods: {
        addNote () {
          this.$emit('addNote', this.note)
        },
      }
  }
  </script>


<style lang="scss">
.new-note {
  text-align: center;
  .btn {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .radio-priority {
    display: flex;
    justify-content: space-between;
    input {
      max-width: 15px;
      margin: 0;
      padding: 0;
      margin-left: 10px;
      &:focus {
        box-shadow: none;
      }
    }
    label {
      margin: 0;
      padding: 0;
    }
    .radio {
      width: 33%;
      display: flex;
      justify-content: center;
    }
  }
}
</style>